import React, { useState, useRef, useEffect} from 'react';
import ReactFileReader from 'react-file-reader';
import './App.css';
import { CSVLink, CSVDownload } from "react-csv";
import srcDie1 from './dice-faces/die1.jpg'
import srcDie2 from './dice-faces/die2.jpg'
import srcDie3 from './dice-faces/die3.jpg'
import srcDie4 from './dice-faces/die4.jpg'
import srcDie5 from './dice-faces/die5.jpg'
import srcDie6 from './dice-faces/die6.jpg'
import srcWhiteDie1 from './dice-faces/whiteDie1.jpg'
import srcWhiteDie2 from './dice-faces/whiteDie2.jpg'
import srcWhiteDie3 from './dice-faces/whiteDie3.jpg'
import srcWhiteDie4 from './dice-faces/whiteDie4.jpg'
import srcWhiteDie5 from './dice-faces/whiteDie5.jpg'
import srcWhiteDie6 from './dice-faces/whiteDie6.jpg'
import { Button } from '@material-ui/core';
import CasinoIcon from '@material-ui/icons/Casino';
import CasinoOutlinedIcon from '@material-ui/icons/CasinoOutlined';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import TextField from '@material-ui/core/TextField';
import PlusOneIcon from '@material-ui/icons/PlusOne';
import ExposureNeg1Icon from '@material-ui/icons/ExposureNeg1';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';

function App() {
  let [file, setFile] = useState('')
  let [cmWidth,setCmWidth] = useState(64)
  let [displayRatio,setDisplayRatio] = useState(1)
  const canvasRef = useRef(null)
  let [isLight,setIsLight] = useState(true)
  let [parentCanvasWidth,setParentCanvasWidth] = useState(600)
  let [done,setDone] = useState(true)
  const canvasParentRef = useRef(null);
  const firstUpdate = useRef(true);
  let [csvData,setcsvData] = useState('hello')
  
  // const export = () = {
  //   this.csvLink.link.click()
  // }



  //handles the dice in the input field and updates cmwidth,which then calls the dicemaker function
  const onChange = (e) => {
    let numberOfDice
    //checks input is an integer
    let input = parseInt(e.target.value)
    //too many dice alert
    if(input>200) {alert('Please enter a number less than 200')}
    //if input is an integer and less than 200
    input && e.target.value< 200 ? numberOfDice = input : numberOfDice = 1
    setCmWidth(parseFloat((numberOfDice*1.6).toFixed(1)))
  }

  //button used to increase mosiac by 1 die
  const increment = () => {
    let newWidth = cmWidth + 1.6
    setCmWidth(parseFloat(newWidth.toFixed(1)))
  }

  //button used to decrease mosiac by 1 die
  const decrement = () => {
    let newWidth = cmWidth - 1.6
    setCmWidth(parseFloat(newWidth.toFixed(1)))
  }

  //loads the files initially and stores src
  const handleFiles = files => {
    const img = new Image()
    img.src = files.base64
    setFile(img.src) 
  }

  //monitors changes in any of the variables in the array and calls the dice mosiac maker
  useEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
    } else {
      setParentCanvasWidth(canvasParentRef.current ? canvasParentRef.current.offsetWidth : 0)
      createPreview()
      setDone(false)
    }
  }, [file,isLight,cmWidth,canvasParentRef.current]);

  

   const createPreview = () => {
      const img = new Image()
      img.src = file
      img.onload = () => {
          //creating the canvas for photomosaic
          const canvas = canvasRef.current
          const context = canvas.getContext('2d')
          let imgWidth = img.naturalWidth
          let imgHeight = img.naturalHeight
  
          //ratio needed to calculate length of the mosiac
          let ratio = imgHeight/imgWidth
          
          //needed to update the text displayed for length in return statement
          setDisplayRatio(ratio)
          
          //dice are 1.6 cm wide, cmWidth is the default number used to calc mosiac size in cms
          let numDiceWidth = Math.ceil(cmWidth / 1.6)
          let numDiceHeight = Math.ceil(ratio * cmWidth / 1.6)
          
          
          //set canvas to max width
          //let fixedCanvasWidth = 600
      
          //needed to calculate the size of the dice images in context of the canvas width
          let size = Math.ceil(parentCanvasWidth / numDiceWidth)

          //sets canvas size to expected dimensions of mosiac
          canvas.width = size * numDiceWidth
          canvas.height = size * numDiceHeight

          //reduce image to size where 1 pixel = 1 die
          context.drawImage(img,0,0,numDiceWidth,numDiceHeight);
          //place reduced image into array for the for loop

          let imgPixels = context.getImageData(0, 0, numDiceWidth, numDiceHeight);
          //clears canvas of the drawn image
          context.clearRect(0, 0, numDiceWidth, numDiceHeight);

          
          
          //makes it so you dont need to type impixels.data in the for loop
          let data = imgPixels.data
          let indexRows = []
          for(let y = 0; y < imgPixels.height; y++){
            let indexColumns = []
            for(let x = 0; x < imgPixels.width; x++){
            //find current row by calc-ing the length and the current count
            let currentRow = (4 * imgPixels.width) * y 
            // find current index within the row
            let currentPixelInRow = (x * 4)
            // sum both in order to find your index in the current row
            let i = currentRow + currentPixelInRow
            //getting the average gray value
            let avg = (data[i] + data[i + 1] + data[i + 2])/3;
            // average gray values
            let grays = [23, 47, 89, 131, 182, 223]
            //approximate avg to the closest gray value
            let gray =  avg <= grays[0] ? grays[0] :
                        avg > grays[0] && avg <= grays[1] ? grays[1] :
                        avg > grays[1] && avg <= grays[2] ? grays[2] :
                        avg > grays[2] && avg <= grays[3] ? grays[3] :
                        avg > grays[3] && avg <= grays[4] ? grays[4] : grays[5]

            //arrays of dice images
            let blackDice = [srcDie1,srcDie2,srcDie3,srcDie4,srcDie5,srcDie6]
            let whiteDice = [srcWhiteDie6,srcWhiteDie5,srcWhiteDie4,srcWhiteDie3,srcWhiteDie2,srcWhiteDie1]
            
            
            indexColumns.push(grays.indexOf(gray))
            //create new dice image 
            const die = new Image()
            isLight ? die.src = whiteDice[grays.indexOf(gray)] : die.src = blackDice[grays.indexOf(gray)]
            //calculates postion of dice based on x and y in for loop
            let xnew = x * size
            let ynew = y * size
            //draws dice image at the desired locations
            context.drawImage(die,xnew,ynew, size, size)
          }
          indexRows.push(indexColumns)
        }
        setDone(true)
        setcsvData(indexRows)
    }
  } 
  //toggles which array to use in mosiac maker function, light or dark
  const checkClick = () => {
    isLight ? setIsLight(false) : setIsLight(true)
  }
  

    return (
      <div className='grid-container'>
      <div class="logo">
           <Typography gutterBottom variant="h3" component="h2" color='primary'>
            Dic<CasinoIcon fontSize='large' />ify
          </Typography>
          
      </div>
      <div className='uploadArea'>
        <div className='upload'>
          <ReactFileReader handleFiles={handleFiles} base64={true}>
              <Button
              variant="contained"
              color="primary"
              startIcon={<CloudUploadIcon />}
            >
              Upload
          </Button>
          </ReactFileReader>
        </div>
        <div className='input'>
          <TextField
            id="standard-helperText"
            label="# Dice"
            value={Math.round(cmWidth/1.6)}
            onChange={(event) => onChange(event) }
          />
        </div>
         
        <div className='inc'>
        <Button
            variant="contained"
            color="primary"
            onClick={increment}
            startIcon={<PlusOneIcon />}
          > 
        </Button>
        </div>
        <div className='dec'>
        <Button
            variant="contained"
            color="primary"
            onClick={decrement}
            startIcon={<ExposureNeg1Icon />}
          > 
        </Button>
        </div>
      </div>

      <div className='widthInfo'>
        <Typography variant="body1" color="textSecondary" component="p">
          {cmWidth.toFixed(1)} cm, {Math.ceil(cmWidth / 1.6)} dice
        </Typography>
      </div>

      <div className='lengthInfo'>
        <div className='length'>
        <Typography variant="body1" id="rotate-text" color="textSecondary" component="p">
          {(cmWidth * displayRatio).toFixed(1)} cm, {Math.ceil(displayRatio * cmWidth / 1.6)} dice
        </Typography>
        </div>
      </div>

      <div ref={canvasParentRef} className ='imgParent'>
        <Card >
          <CardActionArea >
          {done === false && file ?  
              <Typography variant="body1" color="textSecondary" component="p">
                 dicing...
              </Typography> : <div></div>}
          <canvas ref={canvasRef}/>
            <CardContent>
              <Typography variant="body1" color="textSecondary" component="p">
                {Math.ceil(cmWidth / 1.6)*Math.ceil(displayRatio * cmWidth / 1.6)} dice
              </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            
            <Button size="small" color="primary">
              <CSVLink className='link' data={csvData} filename={"dice-mosiac.csv"}>Export</CSVLink>
            </Button>
            <FormControlLabel
              control={
                <Switch label='1' type='checkbox' checked={!isLight} onClick={checkClick} color="inherit"/>}
            label={isLight ? <CasinoOutlinedIcon color='primary' fontSize="medium"/> : <CasinoIcon color='primary' fontSize="medium"/>}/>
          </CardActions>
        </Card>
      </div>
    </div>
    );
}

export default App;
